@tailwind base;
@tailwind components;
@tailwind utilities;


/* 1. Force the top banner iframe to be hidden */
iframe.goog-te-banner-frame {
    display: none !important;
}

/* 2. Prevent the body from being pushed down by the (now hidden) banner */
body {
    top: 0px !important;
    position: relative !important;
}

/* 3. Hide the "Powered by" text and Google logo using a more targeted method */
.goog-te-gadget {
    /* This hides the text node "Powered by" by making its font size zero */
    font-size: 0px !important; 
    color: transparent !important;
}
.goog-logo-link {
    /* This hides the Google logo link */
    display: none !important;
}

/* 4. Style the dropdown <select> element to make it visible and match your UI */
#translation-bar-container select.goog-te-combo {
    cursor: pointer;
    background-color: #4B5563; /* Tailwind gray-600 */
    color: white;
    border: none;
    border-radius: 4px;
    padding: 4px 8px; /* A bit more padding for easier clicking */
    height: 28px; /* Set a fixed height */
    
    /* IMPORTANT: We reset the font size here so the language text is visible */
    font-size: 0.75rem !important; /* text-xs */
}

/* 5. Style the options within the dropdown for readability */
#translation-bar-container select.goog-te-combo option {
    background-color: #374151; /* gray-700 */
    color: white;
}
